"use client";

import { useEffect, useState } from "react";
import DataTable, { TableColumn } from "react-data-table-component";

// ==========================
// TypeScript Transaction Type
// ==========================
type Transaction = {
  id: number;
  merchant_name: string;
  merchant_address: string;
  txn_date: string;
  txn_time: string;
  mid: number;
  tid: number;
  batch_no: number;
  stan: number;
  txn_type: string;
  pan_number: string;
  exp_date: string;
  invoice_number: number;
  card_type: string;
  entry_mode: string;
  approval_code: number;
  rrn: string;
  aid: string;
  application_name: string;
  tvr: string;
  tsi: string;
  amount: number;
  email: string;
  mobile: number;
  created_at: string;
};

const TransactionsPage = () => {
  const [transactions, setTransactions] = useState<Transaction[]>([]);
  const [filteredTransactions, setFilteredTransactions] = useState<Transaction[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string>("");

  // Filter states
  const [merchantFilter, setMerchantFilter] = useState<string>("");
  const [tidFilter, setTidFilter] = useState<string>("");
  const [dateFilter, setDateFilter] = useState<string>("");

  // ==========================
  // Fetch Transactions from API
  // ==========================
  useEffect(() => {
    const fetchTransactions = async () => {
      try {
        const response = await fetch("http://127.0.0.1:8000/transactions"); // Replace with your endpoint
        if (!response.ok) throw new Error(`HTTP error! status: ${response.status}`);
        const data = await response.json();
        setTransactions(data.data);
        setFilteredTransactions(data.data);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };
    fetchTransactions();
  }, []);

  // ==========================
  // Filter Logic
  // ==========================
  useEffect(() => {
    const filtered = transactions.filter((txn) => {
      const matchMerchant = txn.merchant_name.toLowerCase().includes(merchantFilter.toLowerCase());
      const matchTid = txn.tid.toString().includes(tidFilter);
      const matchDate = txn.txn_date.includes(dateFilter);
      return matchMerchant && matchTid && matchDate;
    });
    setFilteredTransactions(filtered);
  }, [merchantFilter, tidFilter, dateFilter, transactions]);

  // ==========================
  // Table Columns
  // ==========================
  const columns: TableColumn<Transaction>[] = [
    { name: "ID", selector: (row) => row.id, sortable: true, width: "60px" },
    { name: "Merchant", selector: (row) => row.merchant_name, sortable: true },
    { name: "Address", selector: (row) => row.merchant_address },
    { name: "Date", selector: (row) => row.txn_date, sortable: true },
    { name: "Time", selector: (row) => row.txn_time },
    { name: "TID", selector: (row) => row.tid.toString(), sortable: true },
    { name: "Card Number", selector: (row) => row.pan_number },
    { name: "Amount", selector: (row) => row.amount.toFixed(2), sortable: true },
    { name: "Email", selector: (row) => row.email },
    { name: "Mobile", selector: (row) => row.mobile.toString() },
  ];

  // ==========================
  // Compact Table Styles
  // ==========================
  const customStyles = {
    rows: {
      style: {
        minHeight: "32px",
        paddingTop: "2px",
        paddingBottom: "2px",
      },
    },
    headCells: {
      style: {
        padding: "8px",
        fontWeight: "bold",
        backgroundColor: "#f3f4f6",
      },
    },
    cells: {
      style: {
        padding: "6px 8px",
      },
    },
  };

  if (loading) return <p>Loading transactions...</p>;
  if (error) return <p className="text-red-500">Error: {error}</p>;

  // ==========================
  // UI Rendering
  // ==========================
  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">Transactions</h1>

      {/* Filter Inputs */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-4">
        <input
          type="text"
          placeholder="Filter by Merchant"
          value={merchantFilter}
          onChange={(e) => setMerchantFilter(e.target.value)}
          className="border border-gray-300 rounded px-3 py-2 w-full"
        />
        <input
          type="text"
          placeholder="Filter by TID"
          value={tidFilter}
          onChange={(e) => setTidFilter(e.target.value)}
          className="border border-gray-300 rounded px-3 py-2 w-full"
        />
        <input
          type="date"
          placeholder="Filter by Date"
          value={dateFilter}
          onChange={(e) => setDateFilter(e.target.value)}
          className="border border-gray-300 rounded px-3 py-2 w-full"
        />
        <button
          onClick={() => {
            setMerchantFilter("");
            setTidFilter("");
            setDateFilter("");
          }}
          className="bg-gray-200 px-4 py-2 rounded hover:bg-gray-300 font-medium"
        >
          Clear Filters
        </button>
      </div>

      {/* DataTable */}
      <div className="bg-white rounded-lg shadow-md p-2">
        <DataTable
          columns={columns}
          data={filteredTransactions}
          pagination
          highlightOnHover
          striped
          dense
          persistTableHead
          customStyles={customStyles}
        />
      </div>
    </div>
  );
};

export default TransactionsPage;
